const pwd = `
pwd
/home/evertonsavio/Documents
`;
const cdBack = `
cd ..
`;
const cdHome = `
cd
`;
const cdToFolder = `
cd minha_pasta
`;
const cdComeBack = `
cd -
`;
const ls = `
ls
`;
const lsr = `
ls -R
`;
const lsa = `
ls -a
`;
const lsal = `
ls -al
`;
const catNew = `
cat > text.txt
`;
const cat = `
cat text.txt
`;
const catFile = `
cat input1.txt input2.txt > output.txt
`;
const catUpper = `
cat input.txt | tr a-z A-Z > output.txt
`;

const AboutLinuxCommands = () => (
  <Slide>
    <SlideWithTitle title="Comandos Linux">
      <h6>Comandos essenciais</h6>
      <img width="250px" src="image/linux-cmd.png" alt="github" />
      <p>Linux</p>
    </SlideWithTitle>
    <Slide transition="slide">
      <h4>pwd</h4>
      <p>
        Use o comando pwd para descobrir o caminho do diretório de trabalho
        atual (pasta) em que você está.
      </p>
      <Bash code={pwd} />
      <p>
        O comando retornará um caminho absoluto (completo), que é basicamente um
        caminho de todos os diretórios que começa com uma barra ( / )
      </p>
    </Slide>
    <Slide transition="slide">
      <h4>cd</h4>
      <p>
        Para navegar pelos arquivos e diretórios do Linux, use o comando cd.
        Requer o caminho completo ou o nome do diretório, dependendo do
        diretório de trabalho atual em que você está.
      </p>
    </Slide>
    <Slide transition="slide">
      <h4>cd</h4>
      <ul>
        <li>Para mover um diretório para cima</li>
        <Bash code={cdBack} />
        <li>Para ir direto para a sua pasta pessoal</li>
        <Bash code={cdHome} />
      </ul>
    </Slide>
    <Slide transition="slide">
      <h4>cd</h4>
      <ul>
        <li>
          para mover para uma pasta. Por exemplo, para mover para uma pasta
          chamada "minha_pasta"
        </li>
        <Bash code={cdToFolder} />
        <li>para mover para o diretório anterior </li>
        <Bash code={cdComeBack} />
      </ul>
    </Slide>
    <Slide transition="slide">
      <h4>ls</h4>
      <p>
        O comando ls é usado para ver o conteúdo de um diretório. Por padrão,
        este comando exibirá o conteúdo do seu diretório de trabalho atual
        <Bash code={ls} />
      </p>
    </Slide>
    <Slide transition="slide">
      <h4>ls</h4>
      <ul>
        <li>
          Existem variações do comando ls:
          <ul>
            <Bash code={lsr} />
            <li>irá listar todos os arquivos nos subdiretórios também</li>
            <Bash code={lsa} />
            <li>ls -a irá mostrar os arquivos ocultos</li>
          </ul>
        </li>
      </ul>
    </Slide>
    <Slide transition="slide">
      <h4>ls</h4>
      <ul>
        <li>
          Mais uma importante variação:
          <ul>
            <Bash code={lsal} />
            <li>
              ls -al irá listar os arquivos e diretórios com informações
              detalhadas como as permissões, tamanho, proprietário, etc.
            </li>
          </ul>
        </li>
      </ul>
    </Slide>
    <Slide transition="slide">
      <h4>cat</h4>
      <p>
        cat (abreviação de concatenate) é um dos comandos mais usados no Linux.
        É usado para listar o conteúdo de um arquivo na saída padrão (sdout).
        Para executar este comando, digite cat seguido do nome do arquivo e sua
        extensão. Por exemplo:
        <Bash code={cat} />
      </p>
    </Slide>
    <Slide transition="slide">
      <h4>cat</h4>
      <p>
        Criar um novo arquivo
        <Bash code={catNew} />
      </p>
      <p>
        Juntar dois arquivos (1 e 2) e armazena a saída deles em um novo arquivo
        (output)
        <Bash code={catFile} />
      </p>
      <p>
        Para converter um arquivo para maiúsculas ou minúsculas,
        <Bash code={catUpper} />
      </p>
    </Slide>
    <Slide transition="slide">
      <h4>cp</h4>
      <p>
        Use o comando cp para copiar arquivos do diretório atual para um
        diretório diferente. Por exemplo, o comando cp scenery.jpg
        /home/username/Pictures criaria uma cópia de scenery.jpg (de seu
        diretório atual) para o diretório Pictures.
      </p>
    </Slide>
    <Slide transition="slide">
      <h4>mv</h4>
      <p>
        O principal uso do comando mv é mover arquivos, embora também possa ser
        usado para renomear arquivos. Os argumentos em mv são semelhantes ao
        comando cp.
      </p>
      <p>
        Você precisa digitar mv, o nome do arquivo e o diretório de destino. Por
        exemplo: mv file.txt /home/username/Documents. Para renomear arquivos, o
        comando Linux é mv oldname.ext newname.ext
      </p>
    </Slide>
    <Slide transition="slide">
      <h4>mkdir</h4>
      <p>
        Use o comando mkdir para criar um novo diretório. Se você digitar mkdir
        Music, ele criará um diretório chamado Music. Também existem comandos
        mkdir extras: Para gerar um novo diretório dentro de outro diretório,
        use este comando básico do Linux mkdir Music/Newfile use a opção p
        (pais) para criar um diretório entre dois diretórios existentes. Por
        exemplo, mkdir -p Music/2020/Newfile.
      </p>
    </Slide>
    <Slide transition="slide">
      <h4>rmdir</h4>
      <p>
        Se você precisar excluir um diretório, use o comando rmdir. No entanto,
        rmdir só permite excluir diretórios vazios.
      </p>
    </Slide>
    <Slide transition="slide">
      <h4>rm</h4>
      <p>
        O comando rm é usado para excluir diretórios e o conteúdo dentro deles.
        Se você deseja apenas excluir o diretório como uma alternativa para
        rmdir, use rm -r. Nota: Tenha muito cuidado com este comando e verifique
        em qual diretório você está. Isso excluirá tudo e não haverá como
        desfazer.
      </p>
    </Slide>
    <Slide transition="slide">
      <h4>touch</h4>
      <p>
        O comando touch permite criar um novo arquivo em branco por meio da
        linha de comando do Linux. Como exemplo, digite touch
        /home/username/Documents/Web.html para criar um arquivo HTML intitulado
        Web no diretório Documentos.
      </p>
    </Slide>
    <Slide transition="slide">
      <h4>find</h4>
      <p>
        Semelhante ao comando locate, o uso de find também procura por arquivos
        e diretórios. A diferença é que você usa o comando find para localizar
        arquivos em um determinado diretório.
      </p>
      <p>
        Como exemplo, o comando find /home/ -name notes.txt irá procurar um
        arquivo chamado notes.txt no diretório home e seus subdiretórios. Para
        procurar diretórios, use / -type d -name notas.txt
      </p>
    </Slide>
    <Slide transition="slide">
      <h4>grep</h4>
      <p>
        Outro comando básico do Linux que é sem dúvida útil para o uso diário é
        o grep. Ele permite que você pesquise todo o texto em um determinado
        arquivo. Para ilustrar, grep blue notepad.txt pesquisará a palavra blue
        no arquivo do bloco de notas. As linhas que contêm a palavra pesquisada
        serão exibidas por completo.
      </p>
    </Slide>
    <Slide transition="slide">
      <h4>sudo</h4>
      <p>
        Abreviação de “SuperUser Do”, este comando permite que você execute
        tarefas que requerem permissões administrativas ou de root. No entanto,
        não é aconselhável usar este comando para uso diário porque pode ser
        fácil ocorrer um erro se você fizer algo errado.
      </p>
    </Slide>
    <Slide transition="slide">
      <h4>df</h4>
      <p>
        Use o comando df para obter um relatório sobre o uso de espaço em disco
        do sistema, mostrado em porcentagem e KBs. Se você quiser ver o
        relatório em megabytes, digite df -m.
      </p>
    </Slide>
    <Slide transition="slide">
      <h4>du</h4>
      <p>
        Se você quiser verificar quanto espaço um arquivo ou diretório ocupa, o
        comando du (Disk Usage) é a resposta. No entanto, o resumo do uso do
        disco mostrará os números dos blocos do disco em vez do formato de
        tamanho usual. Se você quiser vê-lo em bytes, kilobytes e megabytes,
        adicione o argumento -h à linha de comando.
      </p>
    </Slide>
    <Slide transition="slide">
      <h4>echo</h4>
      <p>
        Este comando é usado para mover alguns dados para um arquivo. Por
        exemplo, se você quiser adicionar o texto “Olá, meu nome é John” em um
        arquivo chamado nome.txt, digite:
        <Bash code="echo Olá, meu nome é John >> nome.txt" />
      </p>
    </Slide>
    <Slide transition="slide">
      <h4>head</h4>
      <p>
        O comando head é usado para ver as primeiras linhas de qualquer arquivo
        de texto. Por padrão, ele mostrará as primeiras dez linhas, mas você
        pode alterar esse número como desejar. Por exemplo, se você deseja
        mostrar apenas as primeiras cinco linhas, digite head -n 5 filename.ext.
      </p>
    </Slide>
    <Slide transition="slide">
      <h4>tail</h4>
      <p>
        Este tem uma função semelhante ao comando head, mas em vez de mostrar as
        primeiras linhas, o comando tail exibirá as últimas dez linhas de um
        arquivo de texto. Por exemplo, tail -n filename.ext.
      </p>
    </Slide>
    <Slide transition="slide">
      <h4>diff</h4>
      <p>
        Abreviação de diferença, o comando diff compara o conteúdo de dois
        arquivos linha por linha. Depois de analisar os arquivos, ele produzirá
        as linhas que não coincidem. Os programadores costumam usar esse comando
        quando precisam fazer alterações no programa, em vez de reescrever todo
        o código-fonte. A forma mais simples deste comando é diff file1.ext
        file2.ext
      </p>
    </Slide>
    <Slide transition="slide">
      <h4>tar</h4>
      <p>
        O comando tar é o comando mais usado para arquivar vários arquivos em um
        tarball - um formato de arquivo Linux comum que é semelhante ao formato
        zip, com compactação sendo opcional.
      </p>
      <Bash code="tar -czvf logs_archive.tar.gz *" />
      <Bash code="tar -xzvf logs_archive.tar.gz" />
    </Slide>
    <Slide transition="slide">
      <h4>zip unzip</h4>
      <p>
        Use o comando zip para compactar seus arquivos em um arquivo zip e use o
        comando unzip para extrair os arquivos compactados de um arquivo zip.
      </p>
    </Slide>
    <Slide transition="slide">
      <h4>chown</h4>
      <p>
        No Linux, todos os arquivos pertencem a um usuário específico. O comando
        chown permite que você altere ou transfira a propriedade de um arquivo
        para o nome de usuário especificado. Por exemplo, chown linuxuser2
        file.ext tornará linuxuser2 como o proprietário do file.ext.
      </p>
    </Slide>
    <Slide transition="slide">
      <h4>history</h4>
      <p>
        Quando estiver usando o Linux por um determinado período de tempo, você
        notará rapidamente que pode executar centenas de comandos todos os dias.
        Como tal, executar o comando de histórico é particularmente útil se você
        deseja revisar os comandos que você digitou antes.
      </p>
    </Slide>
    <Slide transition="slide">
      <h4>clear</h4>
      <p> Limpa o terminal </p>
    </Slide>
    <Slide transition="slide">
      <h4>Aula 3 - Exercícios do 1 ao 12</h4>
      <img
        height="250px"
        width="250px"
        src="image/qr-aula-03.png"
        alt="github"
      />
      <p>
        https://github.com/padolabs/web-development-101/tree/master/2.0.0-linux-and-bash/2.1.0-exercises
      </p>
    </Slide>
    <Slide transition="slide">
      <h4>ssh</h4>
      <p>
        Praticamente todos os sistemas Unix e Linux incluem o comando ssh. Este
        comando é usado para iniciar o programa cliente SSH que permite a
        conexão segura com o servidor SSH em uma máquina remota. O comando ssh é
        usado para fazer login na máquina remota, transferir arquivos entre as
        duas máquinas e para executar comandos na máquina remota.
      </p>
      <p>https://www.ssh.com/academy/ssh/command</p>
    </Slide>
    <Slide transition="slide">
      <h4>chmod</h4>
      <p>
        chmod é outro comando do Linux, usado para alterar as permissões de
        leitura, gravação e execução de arquivos e diretórios.
      </p>
    </Slide>
  </Slide>
);

export default AboutLinuxCommands;
